<template>
  <div class="recharge">
    <h3 style="font-size:20px;margin-bottom:30px;"></h3>
    <!--<div class="right-tab">-->
      <!--&lt;!&ndash;&ndash;&gt;-->
      <!--<el-input placeholder="" icon="search" v-model="searchInner" :on-icon-click="handleIconClick" @keyup.enter.native="handleIconClick"></el-input>-->
    <!--</div>-->
    <!---->
    <div class="table">
      <el-table align="center"
                :data="rechargeData"
                style="">
        <el-table-column
          align="center"
          label=""
          prop="code"
          width="">
        </el-table-column>
        <el-table-column
          align="center"
          prop="name"
          label=""
          width="">
        </el-table-column>
        <el-table-column
          align="center"
          prop="phone"
          label=""
          min-width="">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          min-width="">
          <template scope="scope">
              <span v-if="scope.row.normalPrice">{{scope.row.normalPrice}}&nbsp;,&nbsp;{{scope.row.smallPrice}}</span>
              <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="discount"
          label=""
          min-width="">
        </el-table-column>
        <el-table-column
          align="center"
          prop="sum"
          label=""
          min-width="">
        </el-table-column>
        <el-table-column
          align="center"
          prop="yue"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          width="300"
        >
          <template scope="scope">
            <el-button type="text" style="width:40px;" @click="recharge(scope.$index, scope.row.id)"></el-button>
            <el-button type="text" style="width:40px;" @click="rechargeRecord(scope.$index, scope.row.id)"></el-button>
            <el-button type="text" style="width:40px;margin-left:30px;" @click="expenseSetting(scope.$index, scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!---->
    <div class="page">
      <el-pagination style="float: right;"
                     @current-change="handleCurrentChange"
                     layout="total, prev, pager, next"
                     :current-page.sync="currentPage"
                     :page-size="this.limit"
                     :total="totalData">
      </el-pagination>
    </div>
    <!---->
    <div v-show="isRecShow" class="detail">
      <div class="detail-wr" style="">
        <h2 style="border-bottom: 1px solid #ccc;margin-bottom: 15px;padding-bottom: 10px"></h2>
        <el-button type="text" @click="recordCancle" style="color:#000;position: absolute;right: 20px;top: 30px;font-size: 20px;" class="el-icon-close"></el-button>
        <div class="detail-main" style="margin-left: 15px;margin-bottom: 80px;margin-top: 30px;">
          <el-table align="center"
                    :data="recordList"
          >
            <el-table-column
              align="center"
              prop="money"
              label=""
              min-width="150">
            </el-table-column>
            <el-table-column
              align="center"
              prop="payee"
              label=""
              min-width="150">
            </el-table-column>
            <el-table-column
              align="center"
              prop="remark"
              label=""
              min-width="150">
            </el-table-column>
            <el-table-column
              align="center"
              label=""
              min-width="150">
                <template scope="scope">
                  <span>{{scope.row.updatedAt}}</span>
                </template>
            </el-table-column>
          </el-table>
          <!---->
          <!--<div class="page">-->
            <!--<el-pagination style="float: right;"-->
                           <!--@current-change="handleRecPageChange"-->
                           <!--layout="total, prev, pager, next"-->
                           <!--:page-size="this.limitRec"-->
                           <!--:total="this.totalRec">-->
            <!--</el-pagination>-->
          <!--</div>-->
        </div>
      </div>
    </div>
    <!---->
    <div v-show="isShow" class="detail">
      <div class="detail-wr">
        <h2 style="border-bottom: 1px solid #ccc;margin-bottom: 15px;padding-bottom: 10px"></h2>
        <p></p>
        <div class="detail-main" style="margin-left: 60px;">
          <el-form label-width="80px" :model="formData" ref="formData" :rules="rules"> <!--ref-->
            <el-form-item label="" prop="chargeMoney">
              <el-input v-model.number="formData.chargeMoney" style="width: 350px"></el-input>
            </el-form-item>
            <el-form-item label="" prop="chargePerson">
              <el-input v-model="formData.chargePerson" style="width: 350px"></el-input>
            </el-form-item>
            <el-form-item label="" prop="remark">
              <el-input type="textarea" :rows="5" v-model="formData.remark"></el-input>
            </el-form-item>
            <el-form-item style="margin-left: 100px;">
              <el-button type="primary" @click="handlerCharge('formData')"></el-button>
              <el-button @click="handlerCancle('formData')"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
    <!---->
    <div v-show="isSettingShow" class="detail">
      <div class="detail-wr">
        <h2 style="border-bottom: 1px solid #ccc;margin-bottom: 15px;padding-bottom: 10px"></h2>
        <p></p>
        <div class="detail-main" style="margin-left: 60px;">
          <el-form label-width="92px" :model="formSettingData" ref="formSettingData"> <!--ref-->
            <p style="margin:10px 5px 15px 53px;font-size:14px;color:#48576a;"></p>
            <el-form-item label="" style="margin-left:70px;" prop="normalPrice" :rules="{ required: true,validator:normalPrice,trigger: 'blur'}">
              <el-input v-model.number="formSettingData.normalPrice" style="width: 350px"></el-input>
            </el-form-item>
            <el-form-item label="" style="margin-left:70px;" prop="smallPrice" :rules="{ required: true,validator:smallPrice,trigger: 'blur'}">
              <el-input v-model="formSettingData.smallPrice" style="width: 350px"></el-input>
            </el-form-item>
            <!--<el-checkbox-group v-model="formSettingData.roles" style="margin-bottom:20px;margin-left:120px;">
              <span style="margin:0 25px 15px -77px;font-size:12px;"></span>
              <el-checkbox label="acc">0.1</el-checkbox>
              <el-checkbox label="leg">0.12</el-checkbox>
              <el-checkbox label="biz">0.15</el-checkbox>
            </el-checkbox-group>-->
            <el-form-item label=" ">
              <el-radio-group v-model="formSettingData.discount">
                <el-radio :label="0.1"></el-radio>
                <el-radio :label="0.12"></el-radio>
                <el-radio :label="0.15"></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="">
              <el-input type="textarea" :rows="5" v-model="formSettingData.remark"></el-input>
            </el-form-item>
            <el-form-item style="margin-left: 100px;">
              <el-button type="primary" @click="handlerSetting('formSettingData')"></el-button>
              <el-button @click="handlerSettingCancle('formSettingData')"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>
<style>

</style>
<script>
  import util from '../../config/util'
  export default{
    data(){
      return{
        rules:{
          chargeMoney:[
          { required: true, message: ''},
          { type: 'number', message: ''}
        ],
          chargePerson:[
            { required: true, message: ''}
          ]
        },
        isShow:false,
        isSettingShow:false,
        isRecShow:false,
        offset:0,
        limit:10,
        totalData:0,
        currentPage:1,
        corpId:'',
//        
        rechargeData:[],
        recordList:[],
//        
        formData:{
          chargeMoney:'',
          chargePerson:'',
          remark:''
        },
        //
        formSettingData:{}
      }
    },
    methods:{
      normalPrice(rule, value, callback){
        if (!value) {
          return callback(new Error(''));
        }
        if(!(/^\d*$/.test(value))){
          callback(new Error(""));
        }else{
          callback();
        }
      },
      smallPrice(rule, value, callback){
        if (!value) {
          return callback(new Error(''));
        }
        if(!(/^\d*$/.test(value))){
          callback(new Error(""));
        }else{
          callback();
        }
      },
      //
      handleCurrentChange(val){
        var self=this;
        var get_token=sessionStorage.getItem('token');
        var instance = this.$axios.create({
          headers: {'x-access-token': get_token}
        });
        instance.get(process.env.API_HOST+'corp/recharge/sum', {
            params: {
              offset:(val-1)*self.limit,
              limit:self.limit
            }
          })
          .then(function (response) {
            if(response.data.success==true){
              self.rechargeData=response.data.data;
              self.currentPage=val;
            }
          })
          .catch(function (error) {
            console.log(error,"");
          });
      },
      recharge(a,corpId){
          if(this.rechargeData[a].active){
            this.isShow=true;
            this.corpId=corpId;
          }else{
            this.$alert('', '', {
              confirmButtonText: ''
            });
          }
      },
      rechargeRecord(a,corpId){
        this.corpId=corpId;
        var self=this;
        var get_token=sessionStorage.getItem('token');
        var instance = this.$axios.create({
          headers: {'x-access-token': get_token}
        });
        instance.get(process.env.API_HOST+'corp/recharge/list/'+this.corpId)
          .then(function (response) {
            if(response.data.success==true){
              self.recordList=response.data.data.rows;
              self.totalRec=response.data.data.count;
              self.isRecShow=true;
              self.recordList.forEach(function (ele) {
                ele.updatedAt=util.formatDate(new Date(ele.updatedAt));
              })
            }
          })
          .catch(function (error) {
            console.log(error,"");
          });
      },
      handlerCancle(formName){
        this.isShow=false;
        this.$refs[formName].resetFields();
      },
      recordCancle(){
        this.isRecShow=false;
      },
      handlerCharge(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var postData={
              "money":parseInt(this.formData.chargeMoney),
              "payee":this.formData.chargePerson,
              "remark":this.formData.remark
            }
            var self=this;
            var get_token=sessionStorage.getItem('token');
            var instance = this.$axios.create({
              headers: {'x-access-token':get_token
                ,'Content-Type':'application/json'}
            });
            instance.post(process.env.API_HOST+'corp/recharge/add/'+this.corpId,postData)
              .then(function (response) {
                if(response.data.success){
                  self.$message('');
                  instance.get(process.env.API_HOST+'corp/recharge/sum', {
                      params: {
                        offset:(self.currentPage-1)*self.limit,
                        limit:self.limit
                      }
                    })
                    .then(function (response) {
                      if(response.data.success==true){
                        self.rechargeData=response.data.data;
                      }
                    })
                    .catch(function (error) {
                      console.log(error,"");
                    });
                }else{
                  self.$message('');
                }
                self.isShow=false;
                self.$refs[formName].resetFields();
              })
              .catch(function (error){
                self.$message('');
              });
          }
        });
      },
      //
      expenseSetting(index,id){
        this.corpId=id;
        var self=this;
        var get_token=sessionStorage.getItem('token');
        var instance = this.$axios.create({
          headers: {'Content-Type':'application/json','x-access-token':get_token}
        });
        instance.get(process.env.API_HOST+'corp/chargeset/'+id)
          .then(function (response) {
              if(response.data.success){
                self.isSettingShow=true;
                self.formSettingData=response.data.data;
                console.log(self.formSettingData)
              }else{
                self.$alert('', '', {
                  confirmButtonText: ''
                });
              }
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      handlerSetting(formName){
        this.formSettingData.smallPrice=this.formSettingData.smallPrice+'';
        this.formSettingData.normalPrice=this.formSettingData.normalPrice+'';
        this.formSettingData.discount=this.formSettingData.discount+'';
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var self=this;
            var get_token=sessionStorage.getItem('token');
            var instance = this.$axios.create({
              headers: {'Content-Type':'application/json','x-access-token':get_token}
            });
            instance.post(process.env.API_HOST+'corp/chargeset/update/'+this.corpId,this.formSettingData)
              .then(function (response) {
                if(response.data.success){
                  self.isSettingShow=false;
                }
              })
              .catch(function (error) {
                console.log(error);
              });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      handlerSettingCancle(formName){
        //this.$refs[formName].resetFields();
        this.isSettingShow=false;
      }
    },//0549b0f7-dd06-45c9-a722-9151ff9f08c1
    mounted(){
      if(sessionStorage.getItem('token')){
        var self=this;
        var get_token=sessionStorage.getItem('token');
        var instance = this.$axios.create({
          headers: {'x-access-token': get_token}
        });
        instance.get(process.env.API_HOST+'corp/recharge/sum', {
            params: {
              offset:self.offset,
              limit:self.limit
            }
          })
          .then(function (response) {
            if(response.data.success==true){
                //console.log(response.data.data)
              self.rechargeData=response.data.data;
//              
              self.totalData=response.data.count;
              self.currentPage=1;
            }
          })
          .catch(function (error) {
            console.log(error,"");
          });
      }else{
        this.$router.push({path:'/loginView'})
      }
    }
  }
</script>



